.content {
    text-align: center;
    width: 80%;
    margin: 0 auto;
}
.content input {
    width: 80%;
    margin: 10px;
    padding: 18px 10px;
    border-radius: 4px;
    border: 1px solid #4e5766;
    -webkit-box-shadow: 1px 0.5px #888888;
            box-shadow: 1px 0.5px #888888;
    font-size: 18px;
}
.content button {
    padding: 12px 10px;
    width: 30%;
    border-radius: 4px;
    background-color: #4286f4;
    color: #ffffff;
    cursor: pointer;
    font-size: 18px;
}

.tree{
    overflow: scroll;
    height:600px;
    width:90%;
}
.node circle {
	fill: rgb(247, 106, 214);
	stroke: rgb(184, 37, 81);
	stroke-width: 1.5px;
}

.node text {
	font-size: 11px;
	font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
	background-color: black;
	fill: black;
}

.node {
	cursor: pointer;
}

.link {
	fill: none;
	stroke: #2593B8;
	stroke-width: 1.5px;
}

span {
	font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
	color: #F4F4F4;
	text-shadow: 0 1px 4px black;
	float: right;
}



