.Link {
    margin:0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display:-ms-flexbox;
    display:flex;
    height:100%;
    -ms-flex-align:center;
        align-items:center;
}

.Link a{
    color:white;
    text-decoration:none;
    height:100%;
    padding:16px 10px;
    
    -webkit-box-sizing: border-box;
    
            box-sizing: border-box;
    display:block;
}

.Link a:hover,
.Link a:active,
.Link a.active{
    background: brown;
    border-bottom: 4px solid #40A4C8;
}


