.control{
    width: 100%;
    background-color: green;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack:center;
        align-content:center;
    z-index: 2;
    -webkit-box-shadow:0 2px 1px #ccc;
            box-shadow:0 2px 1px #ccc;
    margin-top:20px;
    padding: 10px 0;
}

.controls{
    width: 20%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align:center;
        align-items:center;
    margin:auto;
}

.controls button {
    display:block;
    padding: 5px;
    width: 80px;
    border-radius: 4px;
    color: #ffffff;
    cursor: pointer;
}

.controls button:disabled {
    background-color: #AC9980;
    border: 1px solid #7E7365;
    color: #ccc;
    cursor: default;
}

.controls button:hover:disabled {
    background-color: #AC9980;
    color: #ccc;
    cursor: not-allowed;
}

.Label {
    padding: 10px;
    display:inline-block;
    
}

.controls .Less {  
    background-color: #D39952;
    color: white;
}

.controls .More {
    background-color: #8F5E1E;
    color: white;
}

.controls .Less:hover, .controls .Less:active {  
    background-color: #DAA972;
    color: white;
}

.controls .More:hover,.controls .More:active {
    background-color: #99703F;
    color: white;
}


